<template>
	<article id="content" class="content wid_content_wrap">
		<ul class="breadcrumb">
			<li class="home"><a href="javascript:void(0);">홈</a></li>
			<li><a href="javascript:void(0);">발송관리</a></li>
			<li><span>보낸메시지함</span></li>
		</ul>
		<div class="title_area line">
			<h2>보낸메시지함</h2>
		</div>
		<div class="box_search_area">
			<div class="filter_bundle_wrap">
				<div class="filter_bundle">
					<strong class="tit_bundle">발송일</strong>
					<div class="cont_bundle">
						<div class="box_select_period">
							<div class="period_wrap">
								<span class="custom_input icon_date">
									<vuejs-datepicker :language="ko" :format="customFormatter" :value="new Date()" />
								</span>
								<span class="hypen">~</span>
								<span class="custom_input icon_date">
									<vuejs-datepicker :language="ko" :format="customFormatter" :value="new Date()" />
								</span>
							</div>
							<div class="btn_wrap">
								<span class="custom_checkbox fs14">
									<input type="checkbox" id="Call">
									<label for="Call" class="i_block">선택 안함</label>
								</span>
							</div>
						</div>
					</div>
				</div>
				<div class="filter_bundle" v-if="tabChange == 0">
					<strong class="tit_bundle pdn">발송 채널</strong>
					<div class="cont_bundle">
						<ul class="chk_list">
							<li>
								<span class="custom_checkbox fs14">
									<input type="checkbox" id="typeChkAll">
									<label for="typeChkAll" class="i_block">전체</label>
								</span>
							</li>
							<li>
								<span class="custom_checkbox fs14">
									<input type="checkbox" id="typeChk1">
									<label for="typeChk1" class="i_block">통합메시지 발송</label>
								</span>
							</li>
							<li>
								<span class="custom_checkbox fs14">
									<input type="checkbox" id="typeChk2">
									<label for="typeChk2" class="i_block">RCS 메시지 발송</label>
								</span>
							</li>
							<li>
								<span class="custom_checkbox fs14">
									<input type="checkbox" id="typeChk3">
									<label for="typeChk3" class="i_block">알림톡 메시지 발송</label>
								</span>
							</li>
							<li>
								<span class="custom_checkbox fs14">
									<input type="checkbox" id="typeChk4">
									<label for="typeChk4" class="i_block">문자메시지 발송</label>
								</span>
							</li>
						</ul>
					</div>
				</div>
				<div class="filter_bundle">
					<div class="cont_flex_bundle full_width">
						<span class="custom_select col_3" v-if="tabChange == 0">
							<select>
								<option value="">브랜드명</option>
								<option value="">Brand01(브랜드명)</option>
								<option value="">Brand02(브랜드명)</option>
							</select>
						</span>
						<span class="custom_select col_2" v-if="tabChange == 0">
							<select>
								<option value="">상품코드</option>
								<option value="">템플릿</option>
								<option value="">RCS SMS</option>
								<option value="">RCS LMS</option>
								<option value="">RCS MMS</option>
							</select>
						</span>
						<span v-if="tabChange == 0" class="custom_input col_7">
							<input type="text" placeholder="발신번호 입력">
						</span>
						<span v-if="tabChange == 1" class="custom_select col_3">
							<select>
								<option value="">발송결과</option>
								<option value="">성공</option>
								<option value="">Fallback성공</option>
								<option value="">실패</option>
								<option value="">전송대기</option>
								<option value="">결과수신대기</option>
							</select>
						</span>
						<span v-if="tabChange == 1" class="custom_select col_3">
							<select>
								<option value="">발신번호</option>
								<option value="">수신번호</option>
								<option value="">발송ID</option>
							</select>
						</span>
						<span v-if="tabChange == 1" class="custom_input col_6">
							<input type="text" placeholder="발신번호 입력">
						</span>
					</div>
				</div>
			</div>
			<div class="btn_wrap center">
				<a href="javascript:void(0);" class="btn mid point"><span>검색</span></a>
			</div>
		</div>

		<div class="search_result wid_search_result">
			<div class="tab_wrap">
				<ul class="tab_nav">
					<li class="active" tab-id="0" @click="myFilter($event)">
						<a href="javascript:void(0);">발송단위</a>
					</li>
					<li tab-id="1" @click="myFilter($event)">
						<a href="javascript:void(0);">수신번호</a>
					</li>
				</ul>
			</div>

			<send-type-grid-view v-if="tabChange == 0" />

			<recv-type-grid-view v-if="tabChange == 1" />

		</div>
	</article>
</template>

<script>
import moment from 'moment';
import SendTypeGridView from '../components/SendTypeGridView';
import RecvTypeGridView from '../components/RecvTypeGridView';

export default {
	name: 'sendBoxPage',
	data() {
		return {
			ko: vdp_translation_ko.js,
			date: new Date(),
			tabChange: 0,
		};
	},
	components: {
		'vuejs-datepicker': vuejsDatepicker,
		SendTypeGridView,
		RecvTypeGridView,
	},
	methods: {
		customFormatter: function (date) {
			return moment(date).format('YYYY.MM.DD');
		},
		myFilter: function (event) {
			if (moment(this.endDate).diff(this.startDate, 'day') > 90) {
				if (!confirm('조회기간이 90일을 초과하는 경우 지연시간이 발생할수 있습니다.\n계속 조회하시겠습니까?')) {
					return;
				}
			}

			var myIndex = event.target.parentElement.getAttribute('tab-id');
			var myLi = document.querySelectorAll('.tab_nav > li');

			for (var i = 0; i < myLi.length; i++) {
				myLi[i].classList.remove('active');
			}
			myLi[myIndex].classList.add('active');

			this.tabChange = myIndex;
		},
	}
};
</script>

